import React from "react";
import styles from "./MenuIcon.module.scss";
import { classNames } from "../../helpers";

const MenuIcon = ({ active, onClick }) => (
  <div
    className={classNames(styles.icon, active ? styles.active : "")}
    onClick={onClick}
  >
    <div></div>
    <div></div>
    <div></div>
    <div></div>
  </div>
);

export default MenuIcon;
