import React from "react";

import Icon from "./Icon";
import Link from "./Link";
import Divider from "../Divider/Divider";

import styles from "./IconWithLink.module.scss";

const IconWithLink = ({ image, link }) => {
  return (
    <div className={styles.iconWithLink}>
      <Icon src={image} alt={link.label} />
      <Divider />
      <Link {...link} />
    </div>
  );
};

export default IconWithLink;
