import React from "react";

import Divider from "../Divider/Divider";
import styles from "./Contact.module.scss";
import IconWithLink from "../IconWithLink/IconWithLink";

import githubIcon from "../../assets/icons/icons8-github-128.png";
import linkedinIcon from "../../assets/icons/icons8-linkedin-200.png";
import Link from "../IconWithLink/Link";

const Contact = () => (
  <div className={styles.container}>
    <h1>contact</h1>
    <Divider />
    <div className={styles.iconsContainer}>
      <IconWithLink
        image={githubIcon}
        link={{ to: "https://github.com/jkicmal", label: "GitHub" }}
      />
      <IconWithLink
        image={linkedinIcon}
        link={{
          to: "https://www.linkedin.com/in/jakub-kicmal-453b87166/",
          label: "LinkedIn",
        }}
      />
    </div>
    <Divider size="md" />
    <Link label="jakub.kicmal@gmail.com" to="mailto:jakub.kicmal@gmail.com" />
  </div>
);

export default Contact;
